.ProductCard{
	background: #ffffff;
	position: relative;
	display: inline-block;
  width: calc(33% - 70px);
  margin: 9px;
  padding: 50px 25px;
  border-radius: 15px;
	box-shadow: 0 0 15px rgba(0, 0, 0, 0.15);
}
.ProductCard *{
	margin: 30px 0;
}
.ProductDesc{
	padding-left: 20px;
	font-style: normal;
	font-weight: normal;
	font-size: 16px;
	line-height: 28px;
	color: #54565A;
	text-align: left;
}
.ProductDesc *{
	margin: 0;
}
.grayText{
	color: #53555a;
}
.ProductCardReal{
	position: absolute;
	top: 0;
	left:0;
