	height: 70vh;
	background: #F6F8F9;
}
.ProductImg{
	margin: 0 auto 30px auto;
	width: 135px;
	height: 135px;
	display: block;
}
.ProductName{
	font-style: normal;
	font-weight: bold;
	font-size: 24px;
	line-height: 34px;
	text-align: center;
	color: #2C2E30;
}
.ProductCost{
	font-style: normal;
	font-weight: bold;
	font-size: 24px;
	line-height: 34px;
	text-align: center;
	color: #4983C0;
}
.ProductOrder{
	position: relative;
	font-style: normal;
	font-weight: bold;
	font-size: 18px;
	line-height: 21px;
	outline: none;
	text-decoration: none;
	text-align: center;
	cursor: pointer;
	color: #2C2E30;
	display: inline;
	margin: 30px 0 0 0;
	padding-left: 10px;
}
.ProductOrder:after, .mainMenu li a:after,.UsWork:after{
	display: block;
	position: absolute;
	left: 0;
	width: 0;
	margin-top: 10px;
	height: 3px;
	background-color: #4983C0;
	content: "";
	transition: width 0.3s ease-out;
}
.mainMenu li a:after,
.UsWork:after{
	bottom: 0;
	transition: all 0.3s ease-out;
}
.ProductOrder:hover:after,
.ProductOrder:focus:after,
.mainMenu li a:hover:after,
.mainMenu li a:focus:after,
.UsWork:hover:after,
.UsWork:focus:after{
	width: 100%;
	transition: all 0.3s ease-out;
}
.ProductImgArrow{
	margin: 0;
	padding: 0 10px;
}
.logo, .mainMenu, .phone{
	vertical-align: middle;
	display: inline-block;
}
.mainMenu li a, .phone, .order, .UsWork{
